@use "../variables.scss" as vars;
@use "../mixins.scss" as mixins;

.table-wrapper {
  @include mixins.gradient-top-border;
  display: flex;
  flex-direction: column;
  min-width: min-content;
  flex-grow: 2;
  margin: 15px 7px 0px 7px;

  border-radius: 10px;
  color: vars.$primary;
  padding: 10px;

  background-color: vars.$table;
}

h4,
h3,
h2,
h1 {
  color: vars.$muted-foreground;
  text-align: center;
}

.table {
  color: vars.$primary;

  text-align: center;

  width: 100%;

  th,
  tr,
  td {
    padding: 5px 15px;
    align-content: center;
    align-items: center;
  }

  tr {
    border-bottom: 1px solid vars.$border;
  }

  tr:hover {
    background-color: vars.$secondary;
    transition: all 0.8s ease;
  }

  .table-row {
    border-radius: 10px;
    border-top: 10px;
  }

  .flag-name {
    display: flex;
    align-items: center;
    justify-content: left;
    width: 100%;
  }
  .td {
    margin: auto;
  }
}

.flagg {
  margin-right: 10px;
}

h1 {
  text-align: center;
  font-size: large;
  color: #f8fafc;
  font-size: 30px;
}

.page-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.title-icon {
  width: 24px;
  height: 24px;
  margin-right: 7px;
}

.no-hover {
  pointer-events: none;
}
